.slider-main{
    .react-multiple-carousel__arrow {
        position: absolute;
        outline: 0;
        transition: all .5s;
       
        z-index: 1000;
        border: 0;
        background: rgba(255, 255, 255, 0.5);
        min-width: 43px;
        height: 243px;
        opacity: 1;
        cursor: pointer
    }
    
    .react-multiple-carousel__arrow:hover {
        background: rgba(218, 17, 17, 0.8)
    }
}