.card-main{

    display: grid;
    grid-template-columns: auto auto auto;
    margin: 40px 0;
    justify-content: space-between;
    padding: 0 10px;
    row-gap: 20px;

    .card{
        border: 1px solid black;
        max-width: 300px;
        max-height: 300px;
        overflow: auto;
        padding: 10px;


        img{
            height: 100%;
            width: 100%;
            transition: transform 1s;

            &:hover{

                transform: scale(1.1);
            }
        }
    }
}